<template>
  <main>
    <div class="row w-100 m-0 p-2">
      <div class="col-lg-6 offset-lg-3 p-0">
        <div class="servicess my-2">
          <h5 class="text-center">Services</h5>
          <div
            class="services-search my-3 sticky-top bg-white shadow-sm p-sm-3"
          >
            <div class="input-group mb-3">
              <input
                v-model="query"
                type="search"
                class="form-control form-control-lg services-search-input"
                :placeholder="
                  queryFor == 'services' ? 'VASXXXXXXX' : 'HRXXXXXX'
                "
              />
            </div>
            <div class="d-grid gap-2 mb-3">
              <button
                :disabled="!query"
                class="btn btn-primary btn-lg"
                type="button"
              >
                Search
              </button>
            </div>
            <p class="p-2">
              Showing results for : <span class="fw-bold">{{ query }}</span>
            </p>
          </div>
          <div class="services-results">
            <div v-if="services.length < 1" class="d-grid gap-2 mb-3">
              <div class="alert alert-danger" role="alert">
                No previous service for the vehicle :
                <span class="fw-bold">{{ query }}</span>
              </div>
              <a
                @click.prevent="newService()"
                class="btn btn-dark btn-lg"
                type="button"
              >
                New Service +
              </a>
            </div>
            <div class="service-list-group">
              <div
                class="service-list-heading row shadow-sm d-none d-sm-none d-lg-flex"
              >
                <div class="col-lg-2 col-md-2 col-sm-12">Vehicle No</div>
                <div class="col-lg-2 col-md-2 col-sm-12">Service ID</div>
                <div class="col-lg-4 col-md-4 col-sm-12">Customer Name</div>
                <div class="col-lg-2 col-md-2 col-sm-12">Last Service</div>
                <div class="col-lg-2 col-md-2 col-sm-12">Bill Amount</div>
              </div>
              <router-link
                class="row service-list w-100 m-0 p-1 my-2 border py-3"
                tag="div"
                :to="'/service/' + service.vehicleNo"
                v-for="service in raw"
                :key="service.serviceId"
              >
                <div class="col-lg-2 col-md-2 col-sm-12 services-list-item">
                  <span class="fw-bold">Vehicle No : </span>
                  <span class="badge bg-dark"> {{ service.vehicleNo }}</span>
                </div>
                <div class="col-lg-2 col-md-2 col-sm-12 services-list-item">
                  <span class="fw-bold">Service ID :</span>
                  {{ service.serviceId }}
                </div>
                <div class="col-lg-4 col-md-4 col-sm-12 services-list-item">
                  <span class="fw-bold"> Customer Name :</span>
                  {{ service.customerName }}
                </div>
                <div class="col-lg-2 col-md-2 col-sm-12 services-list-item">
                  <span class="fw-bold">Last Service </span>:
                  {{ service.lastService }}
                </div>
                <div class="col-lg-2 col-md-2 col-sm-12 services-list-item">
                  <span class="fw-bold">Bill Amount </span>: Rs.{{
                    service.billAmount
                  }}
                </div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>
<script>
export default {
  data() {
    return {
      services: [],
      query: "",
    };
  },
  created() {
    const vehicle = this.$route.query.vehicleNo;
    console.log(vehicle);
    if (vehicle) {
      this.query = vehicle;
    }
  },
  methods: {
    formatDate(date) {
      var d = new Date(date),
        month = "" + (d.getMonth() + 1),
        day = "" + d.getDate(),
        year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;
      return [year, month, day].join("-");
    },
    search() {},
    newService() {
      // Redirecting to add service page
      if (this.query) {
        this.$router.push({
          path: "/services/new",
          query: {
            vehicleNo: this.query.toUpperCase(),
          },
        });
      } else {
        this.$router.push({
          path: "/services/new",
        });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.services {
  &-search {
    @media (max-width: 576px) {
      padding: 1rem 0 0 0;
    }
    &-input {
      text-transform: uppercase;
    }
  }
}
.service-list {
  padding: 0.75rem;
  border-radius: 0.5rem;
  cursor: pointer;
  text-decoration: none;
  color: #232526;
  &:hover {
    background: rgb(240, 240, 240);
  }
  &-group {
    border-radius: 0.5rem;
  }
  &-heading {
    margin-bottom: 0.5rem;
    padding: 0.75rem;
    background: #0d6efd;
    color: #fff;
    border-radius: 0.5rem;
    font-weight: bold;
  }
}
</style>
